  color: #e53637;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 15px;
  display: block;
}

.section-title h2 {
  color: #111111;
  font-weight: 700;
  line-height: 46px;
}

.set-bg {
